:root {
  --font-body: "Kumbh Sans", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --orange: hsl(26, 100%, 55%);
  --paleOrange: hsl(25, 100%, 94%);
  --veryDarkBlue: hsl(220, 13%, 13%);
  --darkGrayishBlue: hsl(219, 9%, 45%);
  --grayishBlue: hsl(220, 14%, 75%);
  --lightGrayishBlue: hsl(223, 64%, 98%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.header__nav {
  display: flex;
  justify-content: space-between;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
}
@media (min-width: 71.875em) {
  .header__nav {
    display: flex;
    align-items: center;
    padding-top: 1.5625rem;
    padding-bottom: 1.5625rem;
    border-bottom: 0.0625rem solid #ebebeb;
  }
}
.header__nav--logo {
  align-items: center;
  justify-content: space-between;
  display: flex;
  gap: 0.9375rem;
}
.header__nav--cart {
  display: flex;
  align-items: center;
  gap: 1.25rem;
}
@media (min-width: 71.875em) {
  .header__nav--cart {
    gap: 2.5rem;
  }
}
.header__nav--links {
  display: flex;
  position: relative;
  align-items: center;
  width: 100%;
}
.header__nav--links a {
  position: relative;
  color: var(--grayishBlue);
  padding: 0 25px;
}
.header__nav--links a:first-child {
  padding-left: 0;
}
.header__nav--links a::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 4px;
  bottom: -10px;
  background: var(--orange);
  opacity: 0;
  top: 58px;
  left: 0;
}
.header__nav--links a:first-child::before {
  display: none;
}
.header__nav--links a:hover::before {
  opacity: 1;
  transition: opacity 600ms ease-in-out;
}
.header__nav--links a::before:nth-child(1) {
  opacity: 0;
}
.header__nav--links a:hover {
  color: black;
  transition: all 600ms ease-in-out;
}

.avatar-img img {
  height: 1.625rem;
  width: 1.5rem;
}
@media (min-width: 71.875em) {
  .avatar-img img {
    height: 3.125rem;
    width: 3.125rem;
  }
}
.avatar-img img:hover {
  border-radius: 50%;
  border: 2px solid var(--orange);
}

.cart-items {
  position: absolute;
  padding: 0 0.4375rem;
  margin-bottom: 1.25rem;
  margin-left: 0.625rem;
  color: white;
  font-size: 0.625rem;
  font-weight: bold;
  background-color: var(--orange);
  border-radius: 0.625rem;
}

@media (min-width: 71.875em) {
  .hideForDesktop {
    display: none;
  }
}

@media (max-width: 71.8125em) {
  .hideForMobile {
    display: none;
  }
}

@media (min-width: 71.875em) {
  .mainContent {
    display: flex;
    align-items: center;
    gap: 100px;
    padding: 0 1.5rem;
    max-width: 69.375rem;
    margin: 0 auto;
  }
}

.slider__container {
  max-width: 62.5rem;
  position: relative;
  margin: auto;
}
@media (min-width: 71.875em) {
  .slider__container {
    padding-top: 5.625rem;
  }
}
.slider__images--image {
  display: none;
}
@media (min-width: 71.875em) {
  .slider__images--image {
    width: 27.8125rem;
    height: 27.8125rem;
  }
}
.slider__images--image img {
  width: 100%;
}
@media (min-width: 71.875em) {
  .slider__images--image img {
    border-radius: 20px;
  }
}
.slider__images--number {
  position: absolute;
  right: 0;
  top: 0;
  font-size: 0.75rem;
  font-weight: bold;
  padding: 0.875rem;
  color: white;
  user-select: none;
}
@media (min-width: 71.875em) {
  .slider__images--number {
    display: none;
  }
}
@media (min-width: 71.875em) {
  .slider__thumbnails {
    display: flex;
    justify-content: space-between;
    padding-top: 2.1875rem;
  }
  .slider__thumbnails img {
    height: 5.5rem;
    width: 5.5rem;
    border-radius: 0.625rem;
  }
}
.slider__prevBtn, .slider__nextBtn {
  position: absolute;
  cursor: pointer;
  margin-top: -1.375rem;
  top: 50%;
  width: auto;
  padding: 0.625rem 0.9375rem;
  color: white;
  font-weight: bold;
  font-size: 1rem;
  transition: 0.6s ease;
  user-select: none;
}
.slider__prevBtn {
  margin-left: 0.625rem;
  border-radius: 50%;
}
.slider__nextBtn {
  right: 0;
  margin-right: 0.625rem;
  border-radius: 50%;
}
.slider__prevBtn:hover, .slider__nextBtn:hover {
  background-color: rgba(0, 0, 0, 0.6);
}

.main {
  display: block;
}

.description__container {
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
}
.description__label {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 0.75rem;
  color: var(--orange);
  letter-spacing: 0.0625rem;
}
.description__text {
  color: var(--darkGrayishBlue);
  line-height: 1.7;
}
.description h1 {
  font-size: 1.75rem;
  margin-top: 0.875rem;
}
@media (min-width: 71.875em) {
  .description h1 {
    font-size: 2.5rem;
  }
}

.section__price {
  display: flex;
  align-items: center;
  gap: 1.25rem;
}
.section__price h2 {
  font-weight: bold;
  margin: 0;
}
.section__price--par1 {
  font-weight: bold;
  background-color: var(--paleOrange);
  color: var(--orange);
  padding: 0.125rem 0.5rem;
  border-radius: 0.3125rem;
  margin: 0;
}
.section__price--par2 {
  font-weight: bold;
  text-decoration: line-through;
  color: var(--grayishBlue);
  margin: 0;
  margin-left: auto;
}
.section__cart {
  padding: 20px 0;
}
@media (min-width: 71.875em) {
  .section__cart {
    display: grid;
    grid-template-columns: 1fr 2fr;
    align-items: center;
    gap: 1.25rem;
  }
}
.section__cart--counter {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--lightGrayishBlue);
  border-radius: 10px;
  box-shadow: 0rem 0.1875rem 0.75rem rgba(0, 0, 0, 0.2);
}
.section__cart--counter a {
  padding: 16px 20px;
}
.section__cart--button {
  margin: 20px auto;
}
@media (min-width: 71.875em) {
  .section__cart--button {
    width: 100%;
  }
}
.section__cart button {
  display: flex;
  justify-content: center;
  gap: 15px;
  align-items: center;
  width: 100%;
  border: none;
  border-radius: 10px;
  padding: 16px;
  background-color: var(--orange);
  color: white;
  font-weight: bold;
  box-shadow: 0 3px 12px var(--orange);
}
.section__cart button:hover {
  cursor: pointer;
  background-color: #ff8b33;
  box-shadow: 0 3px 24px var(--orange);
  transform: scale(1.05);
  transition: all 400ms ease-in-out;
}

.mobile__menu {
  height: 100vh;
  width: 65vw;
  position: absolute;
  z-index: 2;
  background-color: white;
  transform: translateX(-100%);
  transition: transform 700ms ease;
}
.mobile__menu--close {
  padding: 20px 0 40px;
}
.mobile__menu--links {
  display: flex;
  flex-direction: column;
  gap: 20px;
  font-weight: bold;
  font-size: 18px;
}
.mobile__menu--links a {
  color: black;
}
.mobile__menu--links a:hover {
  transform: scale(1.05);
  color: var(--orange);
  transition: all 400ms ease-in-out;
}

.openedMenu {
  transform: translateX(0);
}

.darkBackground {
  background-color: rgba(0, 0, 0, 0.6);
}

.overflow {
  overflow: hidden;
}

.cart__display {
  position: absolute;
  width: calc(100% - 12px);
  margin: 5px;
  top: 8%;
  z-index: 1;
  background-color: white;
  border-radius: 10px;
  transform: translateY(-130%);
  transition: transform 700ms ease;
  box-shadow: 0 0.1875rem 0.75rem rgba(0, 0, 0, 0.2);
}
@media (min-width: 71.875em) {
  .cart__display {
    max-width: 25%;
    right: 10%;
  }
}
.cart__display--title {
  border-bottom: 1px solid #ebebeb;
}
.cart__display--title h4 {
  padding-left: 30px;
}
.cart__display--content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 40px;
  padding: 30px;
  height: 25vh;
}
.cart__display--content p {
  margin: 0 auto;
  color: var(--darkGrayishBlue);
}

.active {
  transform: translateY(0);
}

.cart-wrapper {
  display: flex;
  align-items: center;
}

.cart-image {
  width: 60px;
  height: 60px;
  border-radius: 10px;
  box-shadow: 0 0.1875rem 0.75rem rgba(0, 0, 0, 0.2);
}

.cart-image:hover {
  cursor: pointer;
  transform: scale(1.2);
  transition: transform 400ms ease;
}

.cart-paragraph {
  padding-left: 20px;
}

.cart-deleteBtn {
  cursor: pointer;
  background: none;
  border: none;
  padding-left: 20px;
}

.cart-deleteBtn:hover {
  transform: scale(1.2);
  transition: transform 400ms ease;
}

.cart-deleteBtnImage {
  width: 20px;
  height: 20px;
}

.cart-checkout {
  display: block;
}
@media (min-width: 71.875em) {
  .cart-checkout {
    width: 100%;
  }
}

.cart-checkoutBtn {
  cursor: pointer;
  width: 83vw;
  border: none;
  border-radius: 10px;
  padding: 16px;
  font-weight: bold;
  color: #fff;
  background-color: var(--orange);
  box-shadow: 0 3px 12px var(--orange);
}
@media (min-width: 71.875em) {
  .cart-checkoutBtn {
    width: 100%;
  }
}

.cart-checkoutBtn:hover {
  box-shadow: 0 3px 24px var(--orange);
  transform: scale(1.05);
  background-color: #ff8b33;
  transition: all 400ms ease-in-out;
}